import React ,{useState} from 'react';
import { Link } from 'react-router-dom';
import CustomInput from '../../components/custom-input/custom-input.component';
// import './sign-up.css'

import {Container, Header ,Button} from './sign-up.style';

const SignUp=(props)=>{
    const [email,setEmail]=useState('');
const [password,setPassword]=useState('');
    const handleEmail = event => {
        const { value } = event.target;
        setEmail(value);
      
      };
      const handlePassword = event => {
        const { value } = event.target;
        setPassword(value);
      
      };
    return   <div className='main__page'> 
        <Container>
        <div className="upper">
        <Header>Sign Up</Header>
        <CustomInput
						name='Email'
						type='email'
						value={email}
						handleChange={handleEmail}
						label='Email'
					/>
                    <CustomInput
                                    name='Password'
                                    type='password'
                                    value={password}
                                    handleChange={handlePassword}
                                    label='Password'
                                />
<Button onClick={()=>props.history.push('/home')}>Verify</Button>
        </div>
        <div className="lower">
            <div className="service">
                <div className="remember">

            <input type="checkbox" className="check-box"/>
            <span style={{marginLeft:'10px'}}>Remember me</span>
            </div>
            <div className="healp">
                {/* <Link to='/forget-password'>Need help ? </Link> */}
            </div>
            </div>
            <div className="description">
                <img src="data:image/png;base64," alt="face book logo" className="icon"/>
                <Link to='#' className="facebook" style={{marginLeft:'10px'}}>Sign-up with Facebook</Link>
            </div>

            <div className="to-signup">
                <span className="gray">Have an account?</span>
                <a href="/" className="signup">Signin</a>
            </div>
            
        </div>
        
        </Container>
    

    </div>
}

export default SignUp;